.checkout-item {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 0;
  font-size: 20px;
  align-items: center;

  .image-container {
    width: 23%;
    padding-right: 15px;

    img {
      width: 100%;
      height: 100%;
    }
  }
  .name,
  .quantity,
  .price {
    width: 23%;
    padding-left: 10px;
  }

  .quantity {
    padding-left: 5px;

    .arrow {
      display: inline-block;
      cursor: pointer;
    }

    .value {
      padding: 0 15px 0 15px;
    }
  }

  .remove-button {
    padding-left: 23px;
    cursor: pointer;
  }
}
