.cart-dropdown {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;

  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;
  overflow-y: scroll;

  .cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
  }

  .empty-cart {
    font-size: 18px;
    position: absolute;
    top: 100px;
    margin-left: 15px;
  }

  button {
    margin-top: auto;
  }
}
